$color-background: #000;


.sign {
    height: 100vh;
    background-color: $color-background;

    &__container {
    display: flex;
    height: 90vh;
    }

    &__img {
        display: flex;
        width: 50%;
        // background-image: url('./img/');
        background-repeat: no-repeat;
        background-position: center;
        background-size: cover;
        background-color: #fff;
    }

    &__content {
        width: 50%;
    }

    &__svg {

        &_primary {
            margin: auto;
            fill: #fff;
            height: 225px;
        }

        &_secondary {
            height: 35px;
            color: #fff;
        }
    }

    &__footer {
        display: flex;
        gap: 16px;
    }
        &-link {
            color: #fff;
            font-size: 13px;
            text-decoration: none;
        }   
    }